.personal-area {
	position: relative;
	&__breadcrumbs {
		margin-bottom: 10px;
	}
	&-tab-list {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    @include tablet {
    	justify-content: flex-start;
    	overflow-x: auto;
    	margin-bottom: 20px;
    }
		&__btn {
			margin-right: 10px;
			margin-bottom: 10px;
			font-size: 15px;
			&:last-child {
				margin-right: 0;
			}
			&--exit {
				@extend .personal-area-tab-list__btn;
			}
		}
	}
	&-tab-content {
		display: none;
	}
	&-section {
		width: 100%;
		padding: 10px;
		&__top {
			position: relative;
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-bottom: 20px;
			@include tabletS {
				flex-direction: column;
			}
		}
		&__edit {
			display: none;
			position: fixed;
			align-items: center;
		  justify-content: center;
			top: 0;
			left: 0;
			height: 100vh;
			width: 100%;
			z-index: 1111;
			padding: 10px;
			&-wrapper {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background: $black;
				opacity: .5;
			}
			&-section {
				text-align: center;
			}
			&-title {
				margin: 0 auto;
				max-width: 300px;
				font-size: 17px;
				margin-bottom: 25px;
				@include mobileL {
					font-size: 14px;
				}
			}
			&-container {
				position: relative;
				background: $white;
				width: 100%;
				max-width: 400px;
				margin: 0 auto;
				border: 2px solid $black;
				@include MHtabletM {
		    	overflow: auto;
				}
			}
			&-close {
				position: absolute;
				top: 10px;
				right: 10px;
				font-size: 24px;
			}
		}
		&__bottom {
			text-align: left;
			margin-bottom: 10px;
		}
		&__flex-container {
			display: flex;
			@include tabletL {
				flex-direction: column;
				align-items: center;
			}
		}
		&__aside {
			position: relative;
			margin-right: 50px;
			&:after {
				content: '';
				position: absolute;
				top: 0;
				right: -10%;
				height: 100%;
				width: 2px;
				background: $accent;
			}
			@include tabletL {
				margin-right: 0;
				margin-bottom: 15px;
				&:after {
					display: none;
				}
			}
		}
		&__main {
			width: 100%;
		}
		&__img {
			max-width: 100%;
		}
		&__img-block {
			position: relative;
			width: 200px;
			height: 200px;
		  margin: 0 auto 20px auto;
		  overflow: hidden;
		  @include mobileL {
		  	width: 160px;
		  	height: 160px;
		  }
		}
		&__img-btn {
			position: absolute;
			bottom: 0;
			right: 0;
		}
		&__title {
			color: $accent;
			font-weight: 700;
			font-size: 18px;
			margin-bottom: 10px;
			@include tablet {
				font-size: 15px;
			}
		}
		&__name {
			color: $accent;
			font-size: 20px;
			font-weight: 700;
			text-transform: uppercase;
			@include tablet {
				font-size: 16px;
			}
			@include mobileL {
				font-size: 14px;
				margin-bottom: 10px;
			}
		}
		&__quantity {
			color: $accent;
			font-size: 16px;
			font-weight: 700;
			@include tabletS {
				margin-bottom: 20px;
			}
		}
		&__btn {
			padding: 8px 20px;
			font-size: 16px;
			border-radius: 50px;
			@include tablet {
				padding: 12px 26px;
    		font-size: 14px;
			}
		}
		&__link {
			cursor: pointer;
			color: $accent;
			font-size: 18px;
			@include tablet {
				font-size: 15px;
			}
			& svg path {
				fill: $accent;
			}
		}
		&__clients {
			&-list {
				max-width: 300px;
				margin-bottom: 10px;
			}
			&-item {
				margin-bottom: 10px;
				padding: 10px;
				border-bottom: 2px solid $accent;
			}
			&-dropdown {
				display: none;
				margin-left: 10px;
				font-size: 16px;
				@include mobileL {
					font-size: 14px;
				}
				& a {
					color: $accent;
				}
			}
		}
	}
}