.breadcrumbs {
  padding: 10px;
	display: flex;
	@include tablet {
		flex-wrap: wrap;
	}
  &--bg {
  	background: $second;
  	padding-top: 20px;
  }
	&__link {
		color: $accent;
		margin-right: 15px;
		&+.breadcrumbs__link:before {
			content: "/\00a0";
		  color: $accent;
		  margin-right: 5px;
		}
	}
	& span {
		color: $black;
	}
}